.App {
  text-align: center;
  min-height: 100vh;
  background-color: #282c34;
  color: white;
}

.main-content {
  padding: 20px;
}

.game-container {
  display: flex;
  justify-content: space-between;
  align-items: center;
  max-width: 1200px;
  margin: 0 auto;
  padding: 20px;
}

.player-section {
  flex: 1;
  display: flex;
  flex-direction: column;
  align-items: center;
}

.card-space {
  width: 140px;
  height: 200px;
  border: 2px dashed #666;
  border-radius: 10px;
  margin: 20px;
  display: flex;
  justify-content: center;
  align-items: center;
}

.card {
  width: 120px;
  height: 180px;
  background: white;
  border-radius: 8px;
  display: flex;
  justify-content: center;
  align-items: center;
  font-size: 24px;
  font-weight: bold;
}

.card.red {
  color: red;
}

.card.black {
  color: black;
}

.game-controls {
  display: flex;
  flex-direction: column;
  gap: 15px;
  align-items: center;
}

.draw-button, .reset-button {
  padding: 10px 20px;
  font-size: 16px;
  border-radius: 5px;
  cursor: pointer;
}

.bet-controls {
  display: flex;
  align-items: center;
  gap: 10px;
}

.quick-bet-options {
  display: flex;
  gap: 10px;
} 